.group-availability {
    background-color: #f9fbfd;
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
  }

  .group-availability .header-menu {
    height: 52px;
    left: 304px;
    position: absolute;
    top: 4px;
    width: 384px;
  }


  .header-row {
    display: flex;
    align-items: center;
    gap: 20px;
    margin: 20px;
    position: relative;
    z-index: 1;
    margin-bottom: -22px;
  }

  .grid-cell {
    position: relative;
    height: 30px;
    border: 1px solid #eee;
    background-color: #f9f9f9;
    transition: background-color 0.2s;
  }
  
  .grid-cell[data-tooltip]:hover::after {
    content: attr(data-tooltip);
    position: absolute;
    left: 50%;
    top: -30px;
    transform: translateX(-50%);
    background-color: rgba(0, 0, 0, 0.8);
    color: white;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: 12px;
    white-space: nowrap;
    z-index: 10;
  }
  
  /* Add a small arrow to the tooltip */
  .grid-cell[data-tooltip]:hover::before {
    content: '';
    position: absolute;
    left: 50%;
    top: -6px;
    transform: translateX(-50%);
    border-width: 6px;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.8) transparent transparent transparent;
    z-index: 10;
  }
  
  .sport-selector {
    padding: 8px 12px;
    border: 1px solid #ddd;
    border-radius: 6px;
    font-family: "Inter", Helvetica;
    font-size: 14px;
    color: #2d3f70;
    background-color: white;
    cursor: pointer;
    position: relative;
    z-index: 2;
  }
  
  .sport-selector:hover {
    border-color: #2d3f70;
  }
  
  .sport-selector:focus {
    outline: none;
    border-color: #2d3f70;
    box-shadow: 0 0 0 2px rgba(45, 63, 112, 0.1);
  }

